<template>
<div class="top-bar">
  <div class="flex-dates-and-semels"> 
    <div class="pictures-flex">
      <img :src="require('@/assets/hoshen.png')" >
      <img :src="require('@/assets/tzameret.png')" >
 <img :src="require('@/assets/mivzaim.png')" >
 
       </div>
  <div class="dates-block"> 

   <span >{{hebrewDate}}</span>
      <span >{{date}}</span>
   <span ref="span" :seconds = "seconds" class="big-clock">{{time}}</span>
    </div>   
     </div> 
     
  <h1 class="main-title"> דוחמ"ל - מסך כשירות</h1>
   </div>
    <div class="flex-containers">
      <div class="pos">
      <v-progress-circular :width="3" :size="90" indeterminate color="dark" v-if="!isFinished" > </v-progress-circular>
      </div>
      <MezudaGraphs :hamalsData="data" :mezudaPercent="mezudaPercent"  v-if="isFinished" @hamal-click="showSpecific"  />
      <SpecificHamalDetails v-if="isFinished" :hamalData="data[chosenHamal]"  :hamalName="chosenHamal" />
      </div>
</template>

<script>
import { defineComponent } from 'vue';

import MezudaGraphs from "@/components/MezudaGraphs.vue"
import SpecificHamalDetails from "@/components/SpecificHamalDetails.vue"

import spObject from "@/assets/sp.js"
import Hebcal from "hebcal"
import moment from "moment"

// Components

export default defineComponent({
  name: 'Controller',
  data(){
    return{
      isFinished:false,
    data:'',
    time:'',
    mezudaPercent:'',
    chosenHamal:'השפעה',
    seconds:''
    ,hebrewDate:'',
     date:moment(new Date(),'LLLL','he').format('DD/MM יום dddd ')
    }
  },
  mounted(){
    this.handleClock()
    this.handleHamalRequest()
  },

  methods:{
    handleClock(){
      this.getHebrewDate()
      this.updateClock()
          this.clearIntForTime =setInterval(()=>{
            this.updateClock()
        }
          , 1000);
    },
    handleHamalRequest(){
           this.requestAndCalcPrecentage()
           this.clearIntForHamal =setInterval(()=>{
            this.requestAndCalcPrecentage()
        }
          , 300000);
    },
     async requestAndCalcPrecentage(){
        console.log("re")
        this.data = await spObject.getTools();
        this.calcMezudaPercent()
        this.isFinished = true;
        console.log("412")

       },
        calcMezudaPercent(){
          let sumHamal = 0 ;
          Object.keys(this.data).forEach(hamal=>{
            sumHamal  += this.data[hamal]['overallpercent']
          })
          this.mezudaPercent =  sumHamal /  Object.keys(this.data).length
        },
          showSpecific(hamal){
              this.chosenHamal = hamal;
          },
          updateTime(){
                var date = new Date();
                var h = date.getHours(); // 0 - 23
                var m = date.getMinutes(); // 0 - 59
               
                
                
                h = (h < 10) ? "0" + h : h;
                m = (m < 10) ? "0" + m : m;
                
                
                this.time = h + ":" + m 

                
    
        }
        ,
        updateSeconds(){
              var date = new Date();
              var s = date.getSeconds(); // 0 - 59
              s = (s < 10) ? "0" + s : s;
              this.seconds=":"+ s
            
        },
        updateClock(){
             this.updateSeconds()
              this.updateTime()
        },
   getHebrewDate(){
            var unparsed = new Hebcal.HDate().toString('h');
            var arr = unparsed.split(' ')
            this.hebrewDate = `${arr[0]} ב${arr[1]} ${arr[2].slice(1)}`

        }
  },
  components: {
  MezudaGraphs,
  SpecificHamalDetails
  },
});
</script>

