<template>
    <div class="mezuda-container">
              <h3 class="sutitle"> חמ"ל {{hamalName}} </h3>
              <div class="container"> 
                  
                  <v-progress-circular 
    :rotate="360"
    color="#2A2550"
        :size="160" 
    :width="13"
    :model-value="hamalsSorted['overallpercent']"
      >
      {{Math.ceil(hamalsSorted['overallpercent'])}}%

       </v-progress-circular>
       </div>
             <h3 class="second-title"> כשירות רכיבים בחמ"ל </h3>
             <div class="linear-progress-container" >
                    <div class="progress-and-type-flex" v-for="(toolType,index) in Object.keys(hamalsSorted['overalltools'])" :key="index">
                        
                        <span class="type"> <q-tooltip v-if="hamalsSorted['overalltools'][toolType]['recentReport']"> נבדק לאחרונה ב :  {{setDate(hamalsSorted['overalltools'][toolType]['recentReport'])}} </q-tooltip>{{getHebrewOf(toolType)}} </span>
                    <div class="stripe-container">
                 <v-progress-linear :color="hexAccordingToType(toolType)"
                 rounded
                    height="30px"
                    
                        :model-value="hamalsSorted['overalltools'][toolType]['percent']"
                  > 
<template v-slot:default=" {value} ">
<strong> {{Math.ceil(value)}}</strong>
</template>
                     
                 </v-progress-linear>
                 </div>

                 <button class="takalot-btn" v-ripple  @click=" shownHamal=hamalsSorted['overalltools'][toolType]['tkulim']" v-if="hamalsSorted['overalltools'][toolType]['tkulim'].length"> לתקלות</button>
                    </div>

                  </div>

                  <div class="table-wrapper"  v-if="shownHamal"> 
                  <table>
                  <thead> 
                  <tr>
                    <th>שם הרכיב </th> 
                    <th>ip </th>
                    <th>תקול </th>
                    <th>סיבת עיכוב </th>
                    <th>הערות </th>

                  </tr>
                     </thead>

                  <tbody>
                <tr v-for="tool in shownHamal" :key="tool"> 
                <td>{{tool.Title}}</td>
                <td> {{tool.ip}}</td>
                <td> {{getToolErrors(tool.details.boolChecks)}}</td>
                <td> {{tool.delayCause}}</td>
                <td> {{tool.comments}}</td>


                </tr>
                   </tbody>
                   </table>
</div>
</div>
</template>

<script>
import moment from "moment"
export default {
    data(){
        return{
            shownHamal:"",
            isFinished:false,
            val:98
        }
    },
    methods:{
        setDate(date){
            return moment(new Date(date),'LLLL','he').format('DD/MM יום dddd ')
        },
        hexAccordingToType(type){
            if(type =='phones'){
                return '#BFD1DF'    
            }
            if(type =='VC'){
                return '#92B1B6'    
            }
            
            if(type =='PC'){
                return '#CED2C2'    
            }
            
            if(type =='multimedia'){
                return '#F1C5AE'    
            }
            

            
        },
        getHebrewOf(type){
            if(type =='PC'){
                return 'מחשבים'
            }
            if(type == 'phones'){
                return 'טלפוניה'
            }if( type == 'multimedia'){
                return 'מולטימדיה'
            }
            return 'VC'

        },
         getToolErrors(obj){
            var returnedString= ""
            Object.keys(obj).forEach((item)=>{
                if(!obj[item]){
                    returnedString += item +" ,";
                }
            })
            returnedString = returnedString.slice(0,returnedString.length-1)
            return returnedString
        },
    },
    watch:{
        hamalData:function(){
            console.log("emitted")
            this.shownHamal = false;
            this.hamalsSorted = {...this.hamalData}
    //           Object.keys(this.hamalsSorted).forEach((item)=>{
    //                 this.hamalsSorted[item]['fixedTools'] = this.hamalsSorted[item]['tools'].length - this.hamalsSorted[item]['tkulim'].length
    //                 if(this.hamalsSorted[item]['tools'].length ){
    //                     this.hamalsSorted[item]['percent'] = (this.hamalsSorted[item]['fixedTools'] / this.hamalsSorted[item]['tools'].length) * 100
    //                 }else{
    //                     this.hamalsSorted[item]['percent'] = 0 
    //                 }

    //   })
            
        }
    },
//     updated(){
//   this.hamalsSorted = {...this.hamalData}
//       Object.keys(this.hamalsSorted).forEach((item)=>{
//           this.hamalsSorted[item]['length'] =this.hamalsSorted[item]['tools'].length -this.hamalsSorted[item]['tkulim'].length
      

//       })
//     conls
//         console.log(this.hamalData)
//     },
    beforeMount(){
         this.hamalsSorted = {...this.hamalData}
    //   Object.keys(this.hamalsSorted).forEach((item)=>{
    //      this.hamalsSorted[item]['fixedTools'] = this.hamalsSorted[item]['tools'].length - this.hamalsSorted[item]['tkulim'].length
    //                 if(this.hamalsSorted[item]['tools'].length ){
    //                     this.hamalsSorted[item]['percent'] = (this.hamalsSorted[item]['fixedTools'] / this.hamalsSorted[item]['tools'].length) * 100
    //                 }else{
    //                     this.hamalsSorted[item]['percent'] = 0 
    //                 }

    //     this.isFinished = true
    //   })
        console.log(this.hamalsSorted)
    },
        props:["hamalData","hamalName"]
}
</script>

<style scoped>
.phones{

}
.vc{

}
.pc{

}
.multimedia{

}
table{
    position: relative;
    /* table-layout: fixed; */
    margin:0 auto;
  width: 500px;
  border-collapse: collapse;
  /* height: 350px; */
  border-radius: 20px;
    text-align: center;
}
th, td{
    /* white-space: nowrap; */
    border-bottom: 1px solid #bfbebe;
    padding:0.5em 1em;
}
.table-wrapper{
    margin-top: 15px;
    overflow: auto;
    height: 250px;
}

.table-wrapper table thead th {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: white;
}
.stripe-container{
    width:60%;
    margin-right: 30px;
}
.takalot-btn{
    font-size: 1rem;
    padding: 0.25em 0.5em;
    border-radius: 20px;
    color: black;
        box-shadow: 0 0 15px 0 rgb(0 0 0 / 20%);

}
.type{
    display: inline-block;
    text-align: right;
    font-weight: 100;
    width: 12%;
    font-size: 1.2rem;
}
.progress-and-type-flex{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width:100%;
    gap: 10px;
    background-color: white;
}
.linear-progress-container{
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    max-height: 200px;
    gap: 10px;
    min-width: 350px;
    width: 70%;
    margin:0 auto;
    display: flex;
}
.container{
    font-size: 3rem;
}
.second-title{
     margin-top: 10px;
    text-align: center;
        font-weight: 900;

    /* font-family: var(--mega-bold-font);       */
    color: grey;
    margin-bottom: 6px;
    font-size: 1.4rem;
}
.sutitle{
    text-align: center;
     font-family: var(--mega-bold-font);
     font-size: 1.8rem;
}
.mezuda-container{
    text-align: center;
    padding-top: 10px;
    border-radius: 20px;
    /* box-shadow: 0 0 15px 0 rgb(0 0 0 / 20%); */
    background-color: var(--rubric-clr);
    min-width: 550px    
}
.v-progress-circular{
    margin: 1rem;
}

</style>