import axios from "axios"

const httpObject = {}
const vcTypes = ['VC']
const multimediaTypes  =['פורס','דוחס','עמדת שליטה','יעד','מקור']
const pcTypes  = ['מחשב']
const phonesTypes = ['phone']
const spceficToolType ={}
spceficToolType['עמדת שליטה'] = 15
spceficToolType['דוחס'] = 3
spceficToolType['מקור'] = 3
spceficToolType['יעד'] = 10
spceficToolType['פורס'] = 10
spceficToolType['VC'] = 15
spceficToolType['מחשב'] = 7
spceficToolType['phone'] = 5


const shareportintSiteUrl ="https://portal.army.idf/sites/gdud0383/team"
const $isDjango = window.location.href.includes('portal.army.idf') ? false : true
const djangoUrl = process.env.NODE_ENV == 'development' ? 'http://127.0.0.1:8000' : ""

const url =  $isDjango ? djangoUrl : shareportintSiteUrl +"/_api/web/Lists/"
 function parseAsync(string){
    return new Promise((resolve,reject)=>{
        resolve(JSON.parse(string))
    })
}
httpObject.sortPercents = function(hamalsObject){
    console.log(hamalsObject)
    //filter every hamal
    Object.keys(hamalsObject).forEach(hamal=>{
        const toolsInHamalObject = hamalsObject[hamal]['overalltools']
        //filter every tool in the hamal according to its type 
        Object.keys(toolsInHamalObject).forEach((item)=>{
            console.log( toolsInHamalObject[item])
            toolsInHamalObject[item]['fixedTools'] = toolsInHamalObject[item]['tools'].length - toolsInHamalObject[item]['tkulim'].length
            if(toolsInHamalObject[item]['tools'].length ){
            toolsInHamalObject[item]['percent'] = ( toolsInHamalObject[item]['fixedTools'] /toolsInHamalObject[item]['tools'].length)*100
            }else{
                toolsInHamalObject[item]['percent'] = 0
            }
            hamalsObject[hamal]['overallpercent']  = hamalsObject[hamal]['overallpercent'] - toolsInHamalObject[item]['tkulimPoints']   

    })
    console.log(hamalsObject)

    })
    return hamalsObject

}
    httpObject.sortTkulim =function(toolsObject){
        const tkulimSortedObject = {}
         Object.keys(toolsObject).forEach((hamal)=>{
            tkulimSortedObject[hamal]= {overallpercent:100,isCritical:toolsObject[hamal]['isCritical'],overalltools:{}}
        const  hamalObject = tkulimSortedObject[hamal]["overalltools"]
            toolsObject[hamal]['tools'].forEach((tool)=>{
                if(vcTypes.includes(tool.type)){
                    if(!hamalObject.hasOwnProperty('VC')){
                        hamalObject['VC'] = {tools:[],tkulim:[],tkulimPoints:0,recentReport:toolsObject[hamal]['recentReports']['VC']}
                    }
                    hamalObject['VC'].tools.push(tool);
                    console.log(tool)
                    if(Object.keys(tool.details.boolChecks).some(check=>!tool.details.boolChecks[check])){
                        hamalObject['VC'].tkulim.push(tool);
                        let pointsOFtool = tool.isCritical ? spceficToolType[tool.type] + spceficToolType[tool.type] :spceficToolType[tool.type]
                        hamalObject['VC'].tkulimPoints += pointsOFtool
                    }

                }else if(multimediaTypes.includes(tool.type)){
                    if(!hamalObject.hasOwnProperty('multimedia')){
                        hamalObject['multimedia'] = {tools:[],tkulim:[],tkulimPoints:0,recentReport:toolsObject[hamal]['recentReports']['multimedia']}
                    }
                            hamalObject['multimedia'].tools.push(tool)
                            if(Object.keys(tool.details.boolChecks).some(check=>!tool.details.boolChecks[check])){
                                hamalObject['multimedia'].tkulim.push(tool);
                                let pointsOFtool = tool.isCritical ? spceficToolType[tool.type] + spceficToolType[tool.type] :spceficToolType[tool.type]
                                hamalObject['multimedia'].tkulimPoints += pointsOFtool
                            }

                }else if(pcTypes.includes(tool.type)){
                    if(!hamalObject.hasOwnProperty('PC')){
                        hamalObject['PC'] = {tools:[],tkulim:[],tkulimPoints:0,recentReport:toolsObject[hamal]['recentReports']['PC']}
                    }
                    hamalObject['PC'].tools.push(tool)
                    if(Object.keys(tool.details.boolChecks).some(check=>!tool.details.boolChecks[check])){
                        hamalObject['PC'].tkulim.push(tool);
                        let pointsOFtool = tool.isCritical ? spceficToolType[tool.type] + spceficToolType[tool.type] :spceficToolType[tool.type]
                        hamalObject['PC'].tkulimPoints += pointsOFtool

                    }

                }else if(phonesTypes.includes(tool.type)){
                    if(!hamalObject.hasOwnProperty('phones')){
                        hamalObject['phones'] = {tools:[],tkulim:[],tkulimPoints:0,recentReport:toolsObject[hamal]['recentReports']['phones']}
                    }
                    hamalObject['phones'].tools.push(tool)
                    if(Object.keys(tool.details.boolChecks).some(check=>!tool.details.boolChecks[check])){
                        hamalObject['phones'].tkulim.push(tool);
                        let pointsOFtool = tool.isCritical ? spceficToolType[tool.type] + spceficToolType[tool.type] :spceficToolType[tool.type]
                        hamalObject['phones'].tkulimPoints += pointsOFtool

                    }

                }

            })
    })

        console.log(tkulimSortedObject)
    return tkulimSortedObject
}
httpObject.getTools =  async function() {
    let checks;
    if(!$isDjango){
        const checksResponse = await axios.get(url+"getByTitle('checks')/items")
         checks =  checksResponse.data.value;
    }else{
        const checksResponse = await axios.get(url+"/check/")
         checks =  checksResponse.data;
    }
    //get all checks 
    console.log(checks)
    let checksArray =[];
    let checksFinalObject = {}
  //get the tools from each checks
    checksArray = await axios.all(checks.map(check => {
        console.log(check)
        return  this.getHamals(check[$isDjango ? 'id':'checkId'],check.title,check.isCritical)
    }))

    //arrange array of object that map check to the tools in it
    console.log(checksArray)
    checksArray.forEach((check)=>{
        const hamal = Object.keys(check)[2];
        let finalPush = []
        finalPush = [...finalPush,...check[hamal]]          


        if(checksFinalObject.hasOwnProperty(hamal)){
      
            checksFinalObject[hamal]['tools'] = [...checksFinalObject[hamal]['tools'],...finalPush]

        }else{
 

            checksFinalObject[hamal] = {tools:[],isCritical:check.isCritical,recentReports:{...check.recentReports}}
            checksFinalObject[hamal]['tools'] = [...checksFinalObject[hamal]['tools'],...finalPush]

        }
    })
    console.log(checksFinalObject)
    //sort the tools by its type and by tkulim
    checksFinalObject =this.sortTkulim(checksFinalObject)

    //calculatethe percents of each type 
    checksFinalObject =this.sortPercents(checksFinalObject)

    console.log(checksFinalObject)
    return checksFinalObject
}
httpObject.getHamals = async function(checkId,Title,isCritical) {
    
    const promise = new Promise(async (resolve,reject)=>{
//request the hamals under the same check 
        let toolsArraySepareted;
        if(!$isDjango){
            const hamalsReponse = await axios.get(url+"getByTitle('rooms')/items" ,{params:{$select:'hamalId',$filter:`checkId eq '${checkId}'`}})
            let hamalsId = hamalsReponse.data.value;
    //request the tools from each hamals
            const promises =  await axios.all(hamalsId.map((hamal)=>{
                    return axios.get(url + "getByTitle('trialList2')/items",{params:{$filter:`hamalId eq '${hamal.hamalId}'`}})
            }))
             toolsArraySepareted = promises.map(res =>res.data.value);
        }else{
            const hamalsReponse = await axios.get(url+`/hamal/check/${checkId}/`)
            let hamalsId = hamalsReponse.data;
            console.log(hamalsId)
            console.log(checkId)

             //request the tools from each hamals
             if(hamalsId.length){
                    const promises =  await axios.all(hamalsId.map((hamal)=>{
                       console.log(hamal.hamalId)
                            return axios.get(url + `/tool/hamal/${hamal.hamalId}/`)
                    }))
                    toolsArraySepareted = promises.map(res =>res.data);
        }
        }
       
        console.log(toolsArraySepareted)
 
        //group tools from diffrenet hamals of the same check 
        let toolsArrFinal =[]
        toolsArraySepareted.forEach((hamalTools)=>[
            toolsArrFinal = [...toolsArrFinal,...hamalTools]
        ])


//parse all the tools
        let parsedArr;
        let reports;
        if(!$isDjango){
         parsedArr = await  Promise.all((toolsArrFinal.map(tool=>{
            const    {details,...returned} =tool
        return parseAsync(tool.details).then(val=>{
            return {"details":val,...returned}
        })
            
        })))
        const reportsResponse = await axios.get(url+"getByTitle('reports')/items" ,{params:{$filter:`check eq '${Title}'`,$top:3,$select:'submitDate,mahlaka',$orderby:`submitDate desc,check`}})
         reports = reportsResponse.data.value;
    }else{
        parsedArr = toolsArrFinal
        console.log(Title)
        const reportsResponse = await axios.get(url+`/report/recent/${Title}/`)

         reports = reportsResponse.data;
    }
//get all the last reports in the check 
        
 
        // console.log(reports)
        const objectFilter = {}
        reports.forEach((report)=>{
            if(!objectFilter.hasOwnProperty(report.mahlaka)){
                objectFilter[report.mahlaka] =  report.submitDate
            }
        })
        const finalObject = {}
        finalObject['recentReports'] = objectFilter
        finalObject['isCritical'] = isCritical
        finalObject[Title] =parsedArr
      resolve(finalObject)
    })
    return promise
} 
export default httpObject